$cyan: #4eb7a8;
$dark-cyan: #46ab9d;

$orange-red: #e75735;
$dark-orange-red: #d54c2c;

$gray: #888;
$light-gray: #e5e5e5;
$dark-gray: #4f4f4f;

$white: #fff;
$white-smoke: #f2f2f2;
$rose-white: #fdf4f2;

@mixin font-face($font-family, $font, $type) {
  $weight: 400;
  $style: normal;

  @if $font == 'lato' {
    @if $type == 'black' {
      $weight: 900;
    }
  }

  $font-name: '#{$font}-#{$type}';

  @font-face {
    font-family: $font-family;
    font-style: normal;
    font-weight: $weight;
    src: url('../../assets/fonts/#{$font-name}/#{$font-name}.woff') format('woff'),
      url('../../assets/fonts/#{$font-name}/#{$font-name}.ttf') format('ttf'),
      url('../../assets/fonts/#{$font-name}/#{$font-name}.svg') format('svg');
  }
}

@mixin animation($translateX, $scale) {
  transform: translateX($translateX) scale($scale) rotate(45deg);
}

@keyframes slide-right {
  0%,
  100% {
    @include animation(0, 1);
  }

  50% {
    @include animation(4px, 1.2);
  }
}

@keyframes slide-left {
  0% {
    @include animation(0, 1);
  }

  50% {
    @include animation(-4px, 1.2);
  }

  100% {
    @include animation(0, 1);
  }
}

.billboard {
  border-radius: 0.1875rem;
  color: $white;
  display: flex;
  min-height: 13.25rem;
  min-width: 18rem;

  @media screen and (max-width: 400px) {
    min-width: 16.5rem;
  }

  &_background-color {
    &_cyan {
      background-color: $cyan;
    }

    &_orange-red {
      background-color: $orange-red;
    }
  }

  &__content {
    margin: 0.6875rem 1.25rem 0.3rem;
    width: 67%;

    @media screen and (max-width: 400px) {
      margin: 0.4rem 0.6rem 0;
    }
  }

  &__image {
    height: 13.25rem;
    width: 5.9375rem;
  }

  &__description {
    font-family: Lato, Arial, sans-serif;
    font-size: 0.6875rem;
    font-weight: 400;
    letter-spacing: 0.034375rem;
    margin-left: 0.125rem;
  }

  &__date {
    &_type {
      &_event {
        height: 4.375rem;
        margin-bottom: 1.125rem;
        vertical-align: middle;
      }

      &_new {
        font-size: 0.6875rem;
        letter-spacing: 0.034375rem;
        margin: 0.625rem 0 0.8125rem 0.1875rem;
        text-transform: uppercase;
      }
    }
  }

  &__event-day {
    display: inline-block;
    font-size: 4.5625rem;
    letter-spacing: -0.1825rem;
  }

  &__event-month {
    display: inline-block;
    font-size: 1.875rem;
    margin-left: 0.875rem;
  }

  &__title {
    &_type {
      &_event {
        font-size: 0.8125rem;
        letter-spacing: 0.039375rem;
        line-height: 1.25rem;
        margin-left: 0.0625rem;
        text-transform: uppercase;
      }

      &_new {
        font-size: 1.75rem;
        line-height: 1.625rem;
        margin-top: 0.8125rem;
      }
    }
  }

  &__button {
    margin: 1.25rem auto;
    width: 87%;
  }
}
