$cyan: #4eb7a8;
$dark-cyan: #46ab9d;

$orange-red: #e75735;
$dark-orange-red: #d54c2c;

$gray: #888;
$light-gray: #e5e5e5;
$dark-gray: #4f4f4f;

$white: #fff;
$white-smoke: #f2f2f2;
$rose-white: #fdf4f2;

@mixin font-face($font-family, $font, $type) {
  $weight: 400;
  $style: normal;

  @if $font == 'lato' {
    @if $type == 'black' {
      $weight: 900;
    }
  }

  $font-name: '#{$font}-#{$type}';

  @font-face {
    font-family: $font-family;
    font-style: normal;
    font-weight: $weight;
    src: url('../../assets/fonts/#{$font-name}/#{$font-name}.woff') format('woff'),
      url('../../assets/fonts/#{$font-name}/#{$font-name}.ttf') format('ttf'),
      url('../../assets/fonts/#{$font-name}/#{$font-name}.svg') format('svg');
  }
}

@mixin animation($translateX, $scale) {
  transform: translateX($translateX) scale($scale) rotate(45deg);
}

@keyframes slide-right {
  0%,
  100% {
    @include animation(0, 1);
  }

  50% {
    @include animation(4px, 1.2);
  }
}

@keyframes slide-left {
  0% {
    @include animation(0, 1);
  }

  50% {
    @include animation(-4px, 1.2);
  }

  100% {
    @include animation(0, 1);
  }
}

.enroll {
  &__form {
    width: 40%;

    @media screen and (max-width: 800px) {
      margin-bottom: 2rem;
      width: 85%;
    }

    @media screen and (max-width: 400px) {
      margin-bottom: 2rem;
      width: 100%;
    }
  }

  &__title {
    color: $dark-gray;
    font-size: 1.5rem;
    margin: 1rem 1.35rem 1.35rem;
    text-align: center;

    @media screen and (max-width: 600px) {
      font-size: 1.2rem;
    }
  }

  &__content {
    align-items: center;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    height: 31.25rem;
    justify-content: space-between;

    @media screen and (max-width: 800px) {
      height: auto;
    }
  }

  &__datepicker {
    margin-top: 2.5rem;
  }
}
