$cyan: #4eb7a8;
$dark-cyan: #46ab9d;

$orange-red: #e75735;
$dark-orange-red: #d54c2c;

$gray: #888;
$light-gray: #e5e5e5;
$dark-gray: #4f4f4f;

$white: #fff;
$white-smoke: #f2f2f2;
$rose-white: #fdf4f2;

@mixin font-face($font-family, $font, $type) {
  $weight: 400;
  $style: normal;

  @if $font == 'lato' {
    @if $type == 'black' {
      $weight: 900;
    }
  }

  $font-name: '#{$font}-#{$type}';

  @font-face {
    font-family: $font-family;
    font-style: normal;
    font-weight: $weight;
    src: url('../../assets/fonts/#{$font-name}/#{$font-name}.woff') format('woff'),
      url('../../assets/fonts/#{$font-name}/#{$font-name}.ttf') format('ttf'),
      url('../../assets/fonts/#{$font-name}/#{$font-name}.svg') format('svg');
  }
}

@mixin animation($translateX, $scale) {
  transform: translateX($translateX) scale($scale) rotate(45deg);
}

@keyframes slide-right {
  0%,
  100% {
    @include animation(0, 1);
  }

  50% {
    @include animation(4px, 1.2);
  }
}

@keyframes slide-left {
  0% {
    @include animation(0, 1);
  }

  50% {
    @include animation(-4px, 1.2);
  }

  100% {
    @include animation(0, 1);
  }
}

.tick-box {
  cursor: pointer;
  display: inline-block;
  position: relative;

  &__input {
    cursor: pointer;
    height: 0;
    opacity: 0;
    position: absolute;
    width: 0;

    &:checked {
      + .tick-box__check-mark-container {
        background-color: $cyan;

        .tick-box__check-mark {
          border-bottom: 0.125rem solid $white;
          border-left: 0.125rem solid $white;
        }
      }
    }

    &:not(:checked) {
      + .tick-box__check-mark-container {
        background-color: $light-gray;

        .tick-box__check-mark {
          border-bottom: 0.125rem solid #c0c0c0;
          border-left: 0.125rem solid #c0c0c0;
        }
      }
    }
  }

  &__check-mark-container {
    background-color: $cyan;
    border-radius: 50%;
    height: 1.4375rem;
    transition: 0.3s;
    width: 1.5rem;
  }

  &__check-mark {
    border-bottom: 0.125rem solid #000;
    border-left: 0.125rem solid #000;
    height: 0.3125rem;
    left: 25%;
    position: absolute;
    top: 25%;
    transform: rotate(-45deg);
    width: 0.625rem;
  }
}
