$cyan: #4eb7a8;
$dark-cyan: #46ab9d;

$orange-red: #e75735;
$dark-orange-red: #d54c2c;

$gray: #888;
$light-gray: #e5e5e5;
$dark-gray: #4f4f4f;

$white: #fff;
$white-smoke: #f2f2f2;
$rose-white: #fdf4f2;

@mixin font-face($font-family, $font, $type) {
  $weight: 400;
  $style: normal;

  @if $font == 'lato' {
    @if $type == 'black' {
      $weight: 900;
    }
  }

  $font-name: '#{$font}-#{$type}';

  @font-face {
    font-family: $font-family;
    font-style: normal;
    font-weight: $weight;
    src: url('../../assets/fonts/#{$font-name}/#{$font-name}.woff') format('woff'),
      url('../../assets/fonts/#{$font-name}/#{$font-name}.ttf') format('ttf'),
      url('../../assets/fonts/#{$font-name}/#{$font-name}.svg') format('svg');
  }
}

@mixin animation($translateX, $scale) {
  transform: translateX($translateX) scale($scale) rotate(45deg);
}

@keyframes slide-right {
  0%,
  100% {
    @include animation(0, 1);
  }

  50% {
    @include animation(4px, 1.2);
  }
}

@keyframes slide-left {
  0% {
    @include animation(0, 1);
  }

  50% {
    @include animation(-4px, 1.2);
  }

  100% {
    @include animation(0, 1);
  }
}

.message-box {
  background-color: $white-smoke;
  border-radius: 0.25rem;
  width: 18rem;

  @media screen and (max-width: 400px) {
    width: 15.5rem;
  }

  &__header {
    border-radius: inherit;
    height: 7.1875rem;
    position: relative;
    width: 100%;

    @media screen and (max-width: 400px) {
      height: 6rem;
    }

    &::after {
      bottom: 0;
      content: '';
      height: 2.125rem;
      left: 0;
      position: absolute;
      width: 100%;
    }

    &_theme {
      &_cyan {
        background-color: $cyan;

        &::after {
          background-color: $dark-cyan;
        }
      }

      &_orange-red {
        background-color: $orange-red;

        &::after {
          background-color: $dark-orange-red;
        }
      }
    }
  }

  &__name {
    color: $white;
    font-family: 'Source Sans Pro', Arial, sans-serif;
    font-size: 2.125rem;
    letter-spacing: -0.06375rem;
    line-height: 4.375rem;
    text-align: center;

    @media screen and (max-width: 400px) {
      line-height: 3.7rem;
    }
  }

  &__photo {
    bottom: calc(-5.875rem / 2);
    left: calc(50% - 6.3125rem / 2);
    position: absolute;
    z-index: 1;
  }

  &__chat {
    margin: 1.15rem auto 1.8125rem;
    position: relative;
    width: 84.7%;

    @media screen and (max-width: 400px) {
      margin-bottom: 1rem;
    }
  }

  &__buttons {
    height: 2rem;
    position: relative;
  }

  &__camera-button {
    background: url('images/camera-icon.svg') center no-repeat;
    display: inline-block;
    height: 1.5625rem;
    position: absolute;
    right: 0.3125rem;
    width: 1.5625rem;
  }

  &__chat-button {
    background: url('images/chat-icon.svg') center no-repeat;
    display: inline-block;
    height: 1.5625rem;
    left: 0;
    position: absolute;
    top: 0.125rem;
    width: 1.5625rem;
  }

  &__message {
    background-color: $white;
    border-radius: 0.3125rem;
    color: $gray;
    font-size: 0.8125rem;
    min-height: 2rem;
    letter-spacing: 0.039375rem;
    padding: 1.25rem;
    position: relative;
    text-align: left;
    margin-top: 0.7rem;
    width: calc(100% - 2.5rem);

    &::after {
      background-color: inherit;
      border-radius: 0.125rem;
      border-width: 0.375rem;
      bottom: calc(100% - 0.34375rem);
      content: '';
      height: 0.625rem;
      left: 47%;
      margin-top: -0.375rem;
      position: absolute;
      transform: rotate(45deg);
      width: 0.625rem;
    }
  }

  &__reply {
    margin-top: 0.9rem; 
  }

  &__reply-button {
    height: 3.7rem;
    margin-top: 0.85rem;
  }
}
