$cyan: #4eb7a8;
$dark-cyan: #46ab9d;

$orange-red: #e75735;
$dark-orange-red: #d54c2c;

$gray: #888;
$light-gray: #e5e5e5;
$dark-gray: #4f4f4f;

$white: #fff;
$white-smoke: #f2f2f2;
$rose-white: #fdf4f2;

@mixin font-face($font-family, $font, $type) {
  $weight: 400;
  $style: normal;

  @if $font == 'lato' {
    @if $type == 'black' {
      $weight: 900;
    }
  }

  $font-name: '#{$font}-#{$type}';

  @font-face {
    font-family: $font-family;
    font-style: normal;
    font-weight: $weight;
    src: url('../../assets/fonts/#{$font-name}/#{$font-name}.woff') format('woff'),
      url('../../assets/fonts/#{$font-name}/#{$font-name}.ttf') format('ttf'),
      url('../../assets/fonts/#{$font-name}/#{$font-name}.svg') format('svg');
  }
}

@mixin animation($translateX, $scale) {
  transform: translateX($translateX) scale($scale) rotate(45deg);
}

@keyframes slide-right {
  0%,
  100% {
    @include animation(0, 1);
  }

  50% {
    @include animation(4px, 1.2);
  }
}

@keyframes slide-left {
  0% {
    @include animation(0, 1);
  }

  50% {
    @include animation(-4px, 1.2);
  }

  100% {
    @include animation(0, 1);
  }
}

.text-field {
  background-color: $light-gray;
  border: 0;
  border-radius: 0.25rem;
  color: $gray;
  font-family: 'Source Sans Pro', Arial, sans-serif;
  font-size: 0.875rem;
  height: 1.75rem;
  letter-spacing: 0.00875rem;
  outline: none;
  padding-left: 0.75rem;
  padding-right: 0.75rem;
  transition: box-shadow 0.3s;

  &:focus {
    box-shadow: 0 0 0.3125rem #000;
  }

  &_type {
    &_for-search-bar {
      border-radius: 0.25rem 0 0 0.25rem;
      box-sizing: border-box;
      font-size: 0.9rem;
      padding: 0 0 0.125rem 0.9vw;
      width: 100%;
    }

    &_for-message-form {
      @at-root {
        input#{&} {
          font-size: 0.9375rem;
          height: 1.5rem;
          padding-bottom: 0.2125rem;
          padding-left: 0.8125rem;
          width: 64%;
        }

        textarea#{&} {
          height: 4.75rem;
          padding-left: 0.84375rem;
          padding-top: 0.50625rem;
          resize: none;
          width: 91%;
        }
      }
    }

    &_for-message-box {
      border: 0.1875rem solid $white;
      height: 5.5rem;
      padding-left: 1.125rem;
      padding-top: 1rem;
      padding-bottom: 1rem;
      resize: none;
      width: 100%;
      box-sizing: border-box;
    }
  }

  &_theme {
    &_red {
      background-color: $orange-red;
      color: $white;

      &::placeholder {
        color: $white;
      }
    }
  }
}
