$cyan: #4eb7a8;
$dark-cyan: #46ab9d;

$orange-red: #e75735;
$dark-orange-red: #d54c2c;

$gray: #888;
$light-gray: #e5e5e5;
$dark-gray: #4f4f4f;

$white: #fff;
$white-smoke: #f2f2f2;
$rose-white: #fdf4f2;

@mixin font-face($font-family, $font, $type) {
  $weight: 400;
  $style: normal;

  @if $font == 'lato' {
    @if $type == 'black' {
      $weight: 900;
    }
  }

  $font-name: '#{$font}-#{$type}';

  @font-face {
    font-family: $font-family;
    font-style: normal;
    font-weight: $weight;
    src: url('../../assets/fonts/#{$font-name}/#{$font-name}.woff') format('woff'),
      url('../../assets/fonts/#{$font-name}/#{$font-name}.ttf') format('ttf'),
      url('../../assets/fonts/#{$font-name}/#{$font-name}.svg') format('svg');
  }
}

@mixin animation($translateX, $scale) {
  transform: translateX($translateX) scale($scale) rotate(45deg);
}

@keyframes slide-right {
  0%,
  100% {
    @include animation(0, 1);
  }

  50% {
    @include animation(4px, 1.2);
  }
}

@keyframes slide-left {
  0% {
    @include animation(0, 1);
  }

  50% {
    @include animation(-4px, 1.2);
  }

  100% {
    @include animation(0, 1);
  }
}

.footer {
  border: 0.2rem solid #000;
  box-sizing: border-box;
  height: 8rem;
  position: relative;

  @media screen and (max-width: 500px) {
    height: 16rem;
  }

  &_theme {
    &_dark-gray {
      background-color: $dark-gray;
    }
  }

  &__info {
    border-right: 0.2rem solid #000;
    box-sizing: border-box;
    float: left;
    height: 100%;
    padding: 0.5rem 1rem;
    width: 20rem;

    @media screen and (max-width: 500px) {
      border-bottom: 0.2rem solid #000;
      border-right: 0;
      float: none;
      height: 55%;
      text-align: center;
      width: 100%;
    }
  }

  &__name {
    font-size: 2rem;
    margin-bottom: 0.3rem;

    @media screen and (max-width: 500px) {
      font-size: 1.5rem;
    }
  }

  &__navigation {
    float: left;
    padding-top: 1.3rem;
    text-align: center;
    width: calc(100% - 20rem);

    @media screen and (max-width: 500px) {
      float: none;
      padding-top: 0.7rem;
      width: 100%;
    }
  }

  &__search-sign {
    font-size: 1.3rem;
    margin-bottom: 0.3rem;
    text-transform: uppercase;
  }

  &__search-bar {
    margin: auto;
    width: 50%;

    @media screen and (max-width: 722px) {
      width: 80%;
    }
  }

  &__link {
    color: $rose-white;
    display: inline-block;
    margin-top: 0.7rem;
    text-decoration: none;
    text-transform: uppercase;

    &:hover {
      color: $dark-orange-red;
    }
  }

  &__message-us {
    bottom: calc(100% + 0.2rem);
    position: absolute;
    right: 0;
  }
}
