$cyan: #4eb7a8;
$dark-cyan: #46ab9d;

$orange-red: #e75735;
$dark-orange-red: #d54c2c;

$gray: #888;
$light-gray: #e5e5e5;
$dark-gray: #4f4f4f;

$white: #fff;
$white-smoke: #f2f2f2;
$rose-white: #fdf4f2;

@mixin font-face($font-family, $font, $type) {
  $weight: 400;
  $style: normal;

  @if $font == 'lato' {
    @if $type == 'black' {
      $weight: 900;
    }
  }

  $font-name: '#{$font}-#{$type}';

  @font-face {
    font-family: $font-family;
    font-style: normal;
    font-weight: $weight;
    src: url('../../assets/fonts/#{$font-name}/#{$font-name}.woff') format('woff'),
      url('../../assets/fonts/#{$font-name}/#{$font-name}.ttf') format('ttf'),
      url('../../assets/fonts/#{$font-name}/#{$font-name}.svg') format('svg');
  }
}

@mixin animation($translateX, $scale) {
  transform: translateX($translateX) scale($scale) rotate(45deg);
}

@keyframes slide-right {
  0%,
  100% {
    @include animation(0, 1);
  }

  50% {
    @include animation(4px, 1.2);
  }
}

@keyframes slide-left {
  0% {
    @include animation(0, 1);
  }

  50% {
    @include animation(-4px, 1.2);
  }

  100% {
    @include animation(0, 1);
  }
}

.toggle {
  cursor: pointer;
  display: inline-block;
  height: 1.5rem;
  position: relative;
  user-select: none;
  width: 3.75rem;

  &__input {
    height: 0;
    opacity: 0;
    width: 0;

    &:checked + .toggle__slider {
      background-color: $cyan;

      &::before {
        transform: translateX(2.125rem);
      }

      + .toggle__sign_off {
        visibility: hidden;

        + .toggle__sign_on {
          display: block;
        }
      }
    }

    &:not(:checked) + .toggle__slider + .toggle__sign_off {
      visibility: visible;

      + .toggle__sign_on {
        display: none;
      }
    }

    &:focus + .toggle__slider {
      box-shadow: 0 0 0.0625rem $cyan;
    }
  }

  &__slider {
    background-color: $light-gray;
    border-radius: 2.125rem;
    bottom: 0;
    left: 0;
    position: absolute;
    right: 0;
    top: 0;
    transition: 0.4s;

    &::before {
      background-color: #fff;

      border-radius: 50%;
      bottom: 0.1875rem;
      content: '';
      height: 1.125rem;
      left: 0.25rem;
      position: absolute;
      transition: 0.4s;
      width: 1.125rem;
    }
  }

  &__sign {
    color: $white;
    font-size: 0.75rem;
    letter-spacing: 0.0375rem;
    line-height: 0;
    position: absolute;
    text-transform: uppercase;
    top: 50%;

    &_on {
      left: 0.75rem;
    }

    &_off {
      left: 1.6875rem;
    }
  }
}
