$cyan: #4eb7a8;
$dark-cyan: #46ab9d;

$orange-red: #e75735;
$dark-orange-red: #d54c2c;

$gray: #888;
$light-gray: #e5e5e5;
$dark-gray: #4f4f4f;

$white: #fff;
$white-smoke: #f2f2f2;
$rose-white: #fdf4f2;

@mixin font-face($font-family, $font, $type) {
  $weight: 400;
  $style: normal;

  @if $font == 'lato' {
    @if $type == 'black' {
      $weight: 900;
    }
  }

  $font-name: '#{$font}-#{$type}';

  @font-face {
    font-family: $font-family;
    font-style: normal;
    font-weight: $weight;
    src: url('../../assets/fonts/#{$font-name}/#{$font-name}.woff') format('woff'),
      url('../../assets/fonts/#{$font-name}/#{$font-name}.ttf') format('ttf'),
      url('../../assets/fonts/#{$font-name}/#{$font-name}.svg') format('svg');
  }
}

@mixin animation($translateX, $scale) {
  transform: translateX($translateX) scale($scale) rotate(45deg);
}

@keyframes slide-right {
  0%,
  100% {
    @include animation(0, 1);
  }

  50% {
    @include animation(4px, 1.2);
  }
}

@keyframes slide-left {
  0% {
    @include animation(0, 1);
  }

  50% {
    @include animation(-4px, 1.2);
  }

  100% {
    @include animation(0, 1);
  }
}

@import './mixins/font-face';

@each $type in 'regular', 'black' {
  @include font-face('Lato', 'lato', $type);
}

@include font-face('Source Sans Pro', 'source-sans-pro', 'regular');

html {
  background-color: $white;
  color: #000;
  font-family: Lato, Arial, sans-serif;
  font-size: 16px;
  font-style: normal;
  font-weight: normal;

  @media screen and (max-width: 722px) {
    font-size: 15px;
  }
    
  @media screen and (max-width: 400px) {
    font-size: 14px;
  }
}

html, body {
  height: 100%;
  margin: 0;
  padding: 0;
  width: 100%;
}

