$cyan: #4eb7a8;
$dark-cyan: #46ab9d;

$orange-red: #e75735;
$dark-orange-red: #d54c2c;

$gray: #888;
$light-gray: #e5e5e5;
$dark-gray: #4f4f4f;

$white: #fff;
$white-smoke: #f2f2f2;
$rose-white: #fdf4f2;

@mixin font-face($font-family, $font, $type) {
  $weight: 400;
  $style: normal;

  @if $font == 'lato' {
    @if $type == 'black' {
      $weight: 900;
    }
  }

  $font-name: '#{$font}-#{$type}';

  @font-face {
    font-family: $font-family;
    font-style: normal;
    font-weight: $weight;
    src: url('../../assets/fonts/#{$font-name}/#{$font-name}.woff') format('woff'),
      url('../../assets/fonts/#{$font-name}/#{$font-name}.ttf') format('ttf'),
      url('../../assets/fonts/#{$font-name}/#{$font-name}.svg') format('svg');
  }
}

@mixin animation($translateX, $scale) {
  transform: translateX($translateX) scale($scale) rotate(45deg);
}

@keyframes slide-right {
  0%,
  100% {
    @include animation(0, 1);
  }

  50% {
    @include animation(4px, 1.2);
  }
}

@keyframes slide-left {
  0% {
    @include animation(0, 1);
  }

  50% {
    @include animation(-4px, 1.2);
  }

  100% {
    @include animation(0, 1);
  }
}

.donut-chart {
  position: relative;
  user-select: none;

  &__percentage {
    color: #9d9d9d;
    font-size: 2.5rem;
    letter-spacing: -0.125rem;
    line-height: 2.625rem;
    position: absolute;
    text-align: center;
    top: 42px;
    width: 100%;

    &_invisible {
      display: none;
    }
    
  }

  &__animation {
    animation: donut-chart-animation 2s ease-in-out;
    transform: rotate(-90deg);
    transform-origin: center;
    transition: stroke-width 1s;
  }

  &:hover .donut-chart__animation {
    stroke-width: 13%;
  }
}

@keyframes donut-chart-animation {
  from {
    stroke-dasharray: 0;
  }
}
