$cyan: #4eb7a8;
$dark-cyan: #46ab9d;

$orange-red: #e75735;
$dark-orange-red: #d54c2c;

$gray: #888;
$light-gray: #e5e5e5;
$dark-gray: #4f4f4f;

$white: #fff;
$white-smoke: #f2f2f2;
$rose-white: #fdf4f2;

@mixin font-face($font-family, $font, $type) {
  $weight: 400;
  $style: normal;

  @if $font == 'lato' {
    @if $type == 'black' {
      $weight: 900;
    }
  }

  $font-name: '#{$font}-#{$type}';

  @font-face {
    font-family: $font-family;
    font-style: normal;
    font-weight: $weight;
    src: url('../../assets/fonts/#{$font-name}/#{$font-name}.woff') format('woff'),
      url('../../assets/fonts/#{$font-name}/#{$font-name}.ttf') format('ttf'),
      url('../../assets/fonts/#{$font-name}/#{$font-name}.svg') format('svg');
  }
}

@mixin animation($translateX, $scale) {
  transform: translateX($translateX) scale($scale) rotate(45deg);
}

@keyframes slide-right {
  0%,
  100% {
    @include animation(0, 1);
  }

  50% {
    @include animation(4px, 1.2);
  }
}

@keyframes slide-left {
  0% {
    @include animation(0, 1);
  }

  50% {
    @include animation(-4px, 1.2);
  }

  100% {
    @include animation(0, 1);
  }
}

.search-bar {
  background-color: $light-gray;
  border-radius: 0.25rem;
  height: 1.75rem;
  width: 100%;

  &__button {
    background: $orange-red url('images/search-icon.svg') no-repeat center;
    border: 0;
    border-radius: 0 0.25rem 0.25rem 0;
    cursor: pointer;
    float: right;
    height: 1.75rem;
    outline: none;
    width: 2.0625rem;

    &:hover {
      box-shadow: 0 0 0.3125rem #000;
    }

    &:active {
      box-shadow: none;
    }
  }

  &__input {
    float: left;
    width: calc(100% - 2.0625rem);
  }
}
