$cyan: #4eb7a8;
$dark-cyan: #46ab9d;

$orange-red: #e75735;
$dark-orange-red: #d54c2c;

$gray: #888;
$light-gray: #e5e5e5;
$dark-gray: #4f4f4f;

$white: #fff;
$white-smoke: #f2f2f2;
$rose-white: #fdf4f2;

@mixin font-face($font-family, $font, $type) {
  $weight: 400;
  $style: normal;

  @if $font == 'lato' {
    @if $type == 'black' {
      $weight: 900;
    }
  }

  $font-name: '#{$font}-#{$type}';

  @font-face {
    font-family: $font-family;
    font-style: normal;
    font-weight: $weight;
    src: url('../../assets/fonts/#{$font-name}/#{$font-name}.woff') format('woff'),
      url('../../assets/fonts/#{$font-name}/#{$font-name}.ttf') format('ttf'),
      url('../../assets/fonts/#{$font-name}/#{$font-name}.svg') format('svg');
  }
}

@mixin animation($translateX, $scale) {
  transform: translateX($translateX) scale($scale) rotate(45deg);
}

@keyframes slide-right {
  0%,
  100% {
    @include animation(0, 1);
  }

  50% {
    @include animation(4px, 1.2);
  }
}

@keyframes slide-left {
  0% {
    @include animation(0, 1);
  }

  50% {
    @include animation(-4px, 1.2);
  }

  100% {
    @include animation(0, 1);
  }
}

@import '~jquery-ui/themes/base/core.css';
@import '~jquery-ui/themes/base/datepicker.css';

.datepicker {
  background-color: $white-smoke;
  height: 26.8rem;
  width: 17.375rem;

  @media screen and (max-width: 400px) {
    height: 22rem;
    width: 15rem;
  }

  &__header {
    background-color: $orange-red;
    color: $white;
    font-size: 5rem;
    height: 10.5rem;
    line-height: 7rem;
    padding: 0.3125rem 0;
    position: relative;
    text-align: center;
    text-decoration: none;
    width: 100%;

    @media screen and (max-width: 400px) {
      font-size: 4rem;
      height: 8.5rem;
      line-height: 4.5rem;
    }
  }

  &__month-line {
    background-color: $dark-orange-red;
    height: 2.75rem;
    position: absolute;
    top: 6.6875rem;
    width: 100%;

    @media screen and (max-width: 400px) {
      top: 4.6875rem;
    }
  }

  &__datepicker-wrapper {
    position: relative;
    top: -5.3125rem;
    width: 100%;

    .ui-datepicker {
      height: 20rem;
      width: 98%;

      @media screen and (max-width: 400px) {
        height: 17.6rem;
      }

      &-calendar {
        border-collapse: collapse;
        color: $rose-white;
        font-size: 0.7rem;
        height: 14.4rem;
        margin-left: -0.125rem;
        margin-top: 0.3125rem;
        width: 101%;

        @media screen and (max-width: 400px) {
          height: 11.9rem;
        }

        tbody {
          td {
            border: 0.0625rem solid $white;
            height: auto;

            &:hover {
              background-color: $orange-red;
            }

            &:active {
              background-color: $gray;
            }

            a {
              font-size: 1.1rem;
              text-align: center;

              @media screen and (max-width: 400px) {
                font-size: 1rem;
                line-height: 0.8rem;
                padding-top: 0.45rem;
              }
            }
          }
        }

        thead {
          th span {
            position: relative;
            top: 0.1rem;
            right: 0.1rem;
          }
        }

        a {
          color: #868686;
        }
      }

      &-current-day {
        background-color: $orange-red;

        a {
          color: $white;
        }
      }

      &-month {
        color: $white;
        font-size: 1.5rem;
        line-height: 2.8rem;
      }

      &-next {
        border-color: $white $white transparent transparent;
        border-style: solid;
        border-width: 0.25rem;
        cursor: pointer;
        height: 1.0625rem;
        margin-right: 0.375rem;
        margin-top: 1.1875rem;
        transform: scale(0.5) rotate(45deg);
        width: 1.0625rem;
      }

      &-other-month {
        a {
          color: #f9f9f9;

          &:active {
            color: #f9f9f9;
          }
        }
      }

      &-prev {
        border-color: $white $white transparent transparent;
        border-style: solid;
        border-width: 0.25rem;
        cursor: pointer;
        height: 1.0625rem;
        margin-left: 0.375rem;
        margin-top: 1.1875rem;
        transform: scale(0.5) rotate(-135deg);
        width: 1.0625rem;
      }

      &-title {
        background-color: $dark-orange-red;
        height: 2.75rem;
        position: relative;
        top: 0.5rem;
      }

      &-year {
        display: none;
        width: 62.5rem;
      }
    }

    .ui-datepicker-current {
      display: none;
    }
  }

  &__today-button {
    background-color: $light-gray;
    border: 0;
    bottom: 7.5rem;
    color: #868686;
    cursor: pointer;
    font-family: Lato, Arial, sans-serif;
    font-size: 0.9rem;
    height: 3rem;
    letter-spacing: 0.04rem;
    outline: none;
    padding-left: 1.25rem;
    position: relative;
    text-align: left;
    text-transform: uppercase;
    width: 100%;

    @media screen and (max-width: 400px) {
      height: 2.5rem;
    }
  }
}
