$cyan: #4eb7a8;
$dark-cyan: #46ab9d;

$orange-red: #e75735;
$dark-orange-red: #d54c2c;

$gray: #888;
$light-gray: #e5e5e5;
$dark-gray: #4f4f4f;

$white: #fff;
$white-smoke: #f2f2f2;
$rose-white: #fdf4f2;

@mixin font-face($font-family, $font, $type) {
  $weight: 400;
  $style: normal;

  @if $font == 'lato' {
    @if $type == 'black' {
      $weight: 900;
    }
  }

  $font-name: '#{$font}-#{$type}';

  @font-face {
    font-family: $font-family;
    font-style: normal;
    font-weight: $weight;
    src: url('../../assets/fonts/#{$font-name}/#{$font-name}.woff') format('woff'),
      url('../../assets/fonts/#{$font-name}/#{$font-name}.ttf') format('ttf'),
      url('../../assets/fonts/#{$font-name}/#{$font-name}.svg') format('svg');
  }
}

@mixin animation($translateX, $scale) {
  transform: translateX($translateX) scale($scale) rotate(45deg);
}

@keyframes slide-right {
  0%,
  100% {
    @include animation(0, 1);
  }

  50% {
    @include animation(4px, 1.2);
  }
}

@keyframes slide-left {
  0% {
    @include animation(0, 1);
  }

  50% {
    @include animation(-4px, 1.2);
  }

  100% {
    @include animation(0, 1);
  }
}

.arrow-buttons {
  display: flex;
  height: 3.125rem;
  justify-content: space-between;

  &_width {
    &_standard {
      width: 7.8125rem;
    }

    &_100-percents {
      width: 100%;
    }
  }

  &:hover {
    .ripple .arrow-buttons__right-arrow {
      animation: slide-right 1s ease-in-out infinite;
    }

    .ripple .arrow-buttons__left-arrow {
      animation: slide-left 1s ease-in-out infinite;
    }
  }

  &__arrow-container {
    border: 0.1875rem solid $cyan;
    border-radius: 50%;
    height: 2.8125rem;
    cursor: pointer;

    //for ripple-effect:
    overflow: hidden;
    position: relative;
    transition: background-color 0.5s;
    width: 2.8125rem;

    &:hover {
      background-color: $cyan;

      .arrow-buttons__left-arrow, .arrow-buttons__right-arrow {
        border-color: $white;
      }
    }

    &_disabled {
      background-color: $light-gray;
      border-color: $light-gray;
      cursor: default;

      .arrow-buttons__left-arrow, .arrow-buttons__right-arrow {
        animation: none;
        border-color: $white;
        transform: rotate(45deg);
      }

      &:hover {
        background-color: $light-gray;
        border-color: $light-gray;
      }
    }
  }

  &__left-arrow, &__right-arrow {
    height: 0.84375rem;
    margin: 0.84375rem auto;
    width: 0.84375rem;
  }

  &__right-arrow {
    border-right: 0.21875rem solid $cyan;
    border-top: 0.21875rem solid $cyan;
    margin-right: 1.125rem;
    transform: rotate(45deg);
  }

  &__left-arrow {
    border-bottom: 0.21875rem solid $cyan;
    border-left: 0.21875rem solid $cyan;
    margin-left: 1.125rem;
    transform: rotate(45deg);
  }
}
