$cyan: #4eb7a8;
$dark-cyan: #46ab9d;

$orange-red: #e75735;
$dark-orange-red: #d54c2c;

$gray: #888;
$light-gray: #e5e5e5;
$dark-gray: #4f4f4f;

$white: #fff;
$white-smoke: #f2f2f2;
$rose-white: #fdf4f2;

@mixin font-face($font-family, $font, $type) {
  $weight: 400;
  $style: normal;

  @if $font == 'lato' {
    @if $type == 'black' {
      $weight: 900;
    }
  }

  $font-name: '#{$font}-#{$type}';

  @font-face {
    font-family: $font-family;
    font-style: normal;
    font-weight: $weight;
    src: url('../../assets/fonts/#{$font-name}/#{$font-name}.woff') format('woff'),
      url('../../assets/fonts/#{$font-name}/#{$font-name}.ttf') format('ttf'),
      url('../../assets/fonts/#{$font-name}/#{$font-name}.svg') format('svg');
  }
}

@mixin animation($translateX, $scale) {
  transform: translateX($translateX) scale($scale) rotate(45deg);
}

@keyframes slide-right {
  0%,
  100% {
    @include animation(0, 1);
  }

  50% {
    @include animation(4px, 1.2);
  }
}

@keyframes slide-left {
  0% {
    @include animation(0, 1);
  }

  50% {
    @include animation(-4px, 1.2);
  }

  100% {
    @include animation(0, 1);
  }
}

.search-dropdown-profiles {
  align-items: flex-start;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;

  @media screen and (max-width: 722px) {
    align-items: center;
    flex-direction: column;
  }

  &__search-dropdown {
    height: 10rem;
    margin-right: 2.1rem;
    margin-top: 2.75rem;
    width: 44%;

    @media screen and (max-width: 722px) {
      margin-right: 0;
      width: 60%;
    }

    @media screen and (max-width: 400px) {
      width: 100%;
    }
  }

  &__profiles {
    margin-top: 2.75rem;
    width: 40;

    @media screen and (max-width: 722px) {
      width: 60%;
    }

    @media screen and (max-width: 400px) {
      width: 90%;
    }
  }
}
