$cyan: #4eb7a8;
$dark-cyan: #46ab9d;

$orange-red: #e75735;
$dark-orange-red: #d54c2c;

$gray: #888;
$light-gray: #e5e5e5;
$dark-gray: #4f4f4f;

$white: #fff;
$white-smoke: #f2f2f2;
$rose-white: #fdf4f2;

@mixin font-face($font-family, $font, $type) {
  $weight: 400;
  $style: normal;

  @if $font == 'lato' {
    @if $type == 'black' {
      $weight: 900;
    }
  }

  $font-name: '#{$font}-#{$type}';

  @font-face {
    font-family: $font-family;
    font-style: normal;
    font-weight: $weight;
    src: url('../../assets/fonts/#{$font-name}/#{$font-name}.woff') format('woff'),
      url('../../assets/fonts/#{$font-name}/#{$font-name}.ttf') format('ttf'),
      url('../../assets/fonts/#{$font-name}/#{$font-name}.svg') format('svg');
  }
}

@mixin animation($translateX, $scale) {
  transform: translateX($translateX) scale($scale) rotate(45deg);
}

@keyframes slide-right {
  0%,
  100% {
    @include animation(0, 1);
  }

  50% {
    @include animation(4px, 1.2);
  }
}

@keyframes slide-left {
  0% {
    @include animation(0, 1);
  }

  50% {
    @include animation(-4px, 1.2);
  }

  100% {
    @include animation(0, 1);
  }
}

.form-elements {
  &__container {
    align-items: flex-start;
    display: flex;
    justify-content: flex-start;
    margin-top: 2.8rem;

    @media screen and (max-width: 722px) {
      align-items: center;
      flex-direction: column;
    }
  }

  &__message-form-container {
    margin-top: 0.15rem;
    width: 18rem;

    @media screen and (max-width: 722px) {
      margin-bottom: 1.5rem;
      width: 70%;
    }

    @media screen and (max-width: 400px) {
      width: 100%;
    }
  }

  &__toggles-container {
    align-items: center;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    margin-left: 6.375rem;
    width: 8.125rem;

    @media screen and (max-width: 722px) {
      flex-flow: row wrap;
      margin-left: 0;
      margin-top: 1rem;
      width: 90%;
    }

    @media screen and (max-width: 400px) {
      flex-direction: column;
      width: 100%;
    }
  }

  &__tick-boxes {
    margin-right: 0.3125rem;
    margin-top: 2.5rem;
    width: 100%;

    @media screen and (max-width: 722px) {
      margin: 0;
      width: 8rem;
    }

    @media screen and (max-width: 400px) {
      margin-top: 2rem;
    }
  }
}
