$cyan: #4eb7a8;
$dark-cyan: #46ab9d;

$orange-red: #e75735;
$dark-orange-red: #d54c2c;

$gray: #888;
$light-gray: #e5e5e5;
$dark-gray: #4f4f4f;

$white: #fff;
$white-smoke: #f2f2f2;
$rose-white: #fdf4f2;

@mixin font-face($font-family, $font, $type) {
  $weight: 400;
  $style: normal;

  @if $font == 'lato' {
    @if $type == 'black' {
      $weight: 900;
    }
  }

  $font-name: '#{$font}-#{$type}';

  @font-face {
    font-family: $font-family;
    font-style: normal;
    font-weight: $weight;
    src: url('../../assets/fonts/#{$font-name}/#{$font-name}.woff') format('woff'),
      url('../../assets/fonts/#{$font-name}/#{$font-name}.ttf') format('ttf'),
      url('../../assets/fonts/#{$font-name}/#{$font-name}.svg') format('svg');
  }
}

@mixin animation($translateX, $scale) {
  transform: translateX($translateX) scale($scale) rotate(45deg);
}

@keyframes slide-right {
  0%,
  100% {
    @include animation(0, 1);
  }

  50% {
    @include animation(4px, 1.2);
  }
}

@keyframes slide-left {
  0% {
    @include animation(0, 1);
  }

  50% {
    @include animation(-4px, 1.2);
  }

  100% {
    @include animation(0, 1);
  }
}

.message-form {
  position: relative;

  &__data-field {
    &_type {
      &_input {
        margin-bottom: 0.75rem;
      }
    }
  }

  &__tick-boxes {
    float: left;
  }

  &__tick-item {
    align-items: center;
    display: flex;
    margin-top: 0.3125rem;
  }

  &__tick-sign {
    margin-left: 0.625rem;
  }

  &__button {
    float: right;
    letter-spacing: 0.03125rem;
    margin-top: 0.6rem;
  }

  &__tooltip {
    border-radius: 0.25rem;
    color: #fff;
    display: inline-block;
    font-size: 0.625rem;
    letter-spacing: 0.03125rem;
    line-height: 1.625rem;
    margin-top: 0.1rem;
    margin-left: 0.4rem;
    padding-top: 0.09375rem;
    position: absolute;
    text-align: center;
    text-transform: uppercase;
    user-select: none;
    width: 25%;

    &::after {
      border-style: solid;
      border-width: 0.25rem;
      content: '';
      margin-top: -0.25rem;
      position: absolute;
      right: 100%;
      top: 50%;
    }

    &_color {
      &_orange-red {
        background-color: $orange-red;

        &::after {
          border-color: transparent $orange-red transparent transparent;
        }
      }

      &_cyan {
        background-color: $cyan;

        &::after {
          border-color: transparent $cyan transparent transparent;
        }
      }
    }

    &_invisible {
      display: none;
    }
  }
}
