$cyan: #4eb7a8;
$dark-cyan: #46ab9d;

$orange-red: #e75735;
$dark-orange-red: #d54c2c;

$gray: #888;
$light-gray: #e5e5e5;
$dark-gray: #4f4f4f;

$white: #fff;
$white-smoke: #f2f2f2;
$rose-white: #fdf4f2;

@mixin font-face($font-family, $font, $type) {
  $weight: 400;
  $style: normal;

  @if $font == 'lato' {
    @if $type == 'black' {
      $weight: 900;
    }
  }

  $font-name: '#{$font}-#{$type}';

  @font-face {
    font-family: $font-family;
    font-style: normal;
    font-weight: $weight;
    src: url('../../assets/fonts/#{$font-name}/#{$font-name}.woff') format('woff'),
      url('../../assets/fonts/#{$font-name}/#{$font-name}.ttf') format('ttf'),
      url('../../assets/fonts/#{$font-name}/#{$font-name}.svg') format('svg');
  }
}

@mixin animation($translateX, $scale) {
  transform: translateX($translateX) scale($scale) rotate(45deg);
}

@keyframes slide-right {
  0%,
  100% {
    @include animation(0, 1);
  }

  50% {
    @include animation(4px, 1.2);
  }
}

@keyframes slide-left {
  0% {
    @include animation(0, 1);
  }

  50% {
    @include animation(-4px, 1.2);
  }

  100% {
    @include animation(0, 1);
  }
}

@import '~jquery-ui/themes/base/core';
@import '~jquery-ui/themes/base/slider';

.slider {
  &__scale {
    border-radius: 0.625rem;
    height: 0.3125rem;
    width: 100%;

    &_color {
      &_gray {
        background-color: $light-gray;
      }

      &_green {
        background-color: $cyan;
      }
    }

    &_type {
      &_for-stages {
        width: 100%;
      }
    }
  }

  &__range {
    border-radius: 0.625rem;

    &_color {
      &_gray {
        background-color: $light-gray;
      }

      &_green {
        background-color: $cyan;
      }

      &_orange-red {
        background-color: $orange-red;
      }
    }
  }

  .ui-slider-handle#{&}__handle {
    border-radius: 50%;
    cursor: pointer;
    height: 1.21875rem;
    outline: none;
    top: -0.4375rem;
    width: 1.21875rem;

    &_color {
      &_green {
        background-color: $cyan;
      }

      &_orange-red {
        background-color: $orange-red;
      }
    }

    &_type {
      &_for-stages {
        display: none;
      }
    }
  }

  &__tooltip {
    border-radius: 20%;
    font-size: 0.8125rem;
    height: 1.5rem;
    left: -0.5rem;
    letter-spacing: 0.040625rem;
    line-height: 1.75rem;
    position: absolute;
    text-align: center;
    top: -2.0625rem;
    user-select: none;
    width: 2.1875rem;

    &::after {
      border-style: solid;
      border-width: 0.3125rem;
      content: '';
      left: 50%;
      margin-left: -0.34375rem;
      position: absolute;
      top: 100%;
    }

    &_theme {
      &_orange-red {
        background-color: $orange-red;
        color: $white;

        &::after {
          border-color: $orange-red transparent transparent transparent;
        }
      }
    }
  }

  &__labels-line {
    display: none;
  }

  .ui-slider-pip#{&}__labels-pip {
    &_type {
      &_for-labels {
        font-size: 0.6875rem;
        letter-spacing: 0.034375rem;
        top: 0.625rem;
      }

      &_for-stages {
        border-radius: 50%;
        font-size: 0.875rem;
        height: 1.8125rem;
        letter-spacing: 0.04375rem;
        line-height: 1.125rem;
        text-align: center;
        top: -0.75rem;
        width: 1.8125rem;
      }
    }

    &_background-color {
      &_gray {
        background-color: $light-gray;
      }

      &_orange-red {
        background-color: $orange-red;
      }

      &_green {
        background-color: $cyan;
      }
    }

    &_color {
      &_white {
        color: $white;
      }

      &_gray {
        color: #d1d1d1;
      }

      &_dark-gray {
        color: $gray;
      }
    }
  }

  &__labels-label {
    &_type {
      &_for-stages {
        padding-top: 0.0625rem;
        z-index: 1000;
      }
    }
  }
}

