$cyan: #4eb7a8;
$dark-cyan: #46ab9d;

$orange-red: #e75735;
$dark-orange-red: #d54c2c;

$gray: #888;
$light-gray: #e5e5e5;
$dark-gray: #4f4f4f;

$white: #fff;
$white-smoke: #f2f2f2;
$rose-white: #fdf4f2;

@mixin font-face($font-family, $font, $type) {
  $weight: 400;
  $style: normal;

  @if $font == 'lato' {
    @if $type == 'black' {
      $weight: 900;
    }
  }

  $font-name: '#{$font}-#{$type}';

  @font-face {
    font-family: $font-family;
    font-style: normal;
    font-weight: $weight;
    src: url('../../assets/fonts/#{$font-name}/#{$font-name}.woff') format('woff'),
      url('../../assets/fonts/#{$font-name}/#{$font-name}.ttf') format('ttf'),
      url('../../assets/fonts/#{$font-name}/#{$font-name}.svg') format('svg');
  }
}

@mixin animation($translateX, $scale) {
  transform: translateX($translateX) scale($scale) rotate(45deg);
}

@keyframes slide-right {
  0%,
  100% {
    @include animation(0, 1);
  }

  50% {
    @include animation(4px, 1.2);
  }
}

@keyframes slide-left {
  0% {
    @include animation(0, 1);
  }

  50% {
    @include animation(-4px, 1.2);
  }

  100% {
    @include animation(0, 1);
  }
}

.help-form {
  position: relative;

  &__header {
    background-color: $dark-gray;
    border-radius: 0.125rem;
    color: $white;
    cursor: pointer;
    font-size: 1.5rem;
    height: 3.125rem;
    line-height: 3.125rem;
    float: right;
    text-align: center;
    vertical-align: center;
    width: 12rem;
  }

  &__arrow {
    width: 1rem;
    height: 1rem;
    display: inline-block;
    vertical-align: middle;
    border-style: solid;
    border-width: 0.2rem 0.2rem 0 0;
    border-color: $white;
    transform: rotate(-45deg);
    margin-right: 1rem;
    transition: transform 0.5s;

    &_direction {
      &_down {
        transform: rotate(135deg);
      }
    }
  }

  &__message-box {
    clear: both;

    &_invisible {
      display: none;
    }
  }
}
