$cyan: #4eb7a8;
$dark-cyan: #46ab9d;

$orange-red: #e75735;
$dark-orange-red: #d54c2c;

$gray: #888;
$light-gray: #e5e5e5;
$dark-gray: #4f4f4f;

$white: #fff;
$white-smoke: #f2f2f2;
$rose-white: #fdf4f2;

@mixin font-face($font-family, $font, $type) {
  $weight: 400;
  $style: normal;

  @if $font == 'lato' {
    @if $type == 'black' {
      $weight: 900;
    }
  }

  $font-name: '#{$font}-#{$type}';

  @font-face {
    font-family: $font-family;
    font-style: normal;
    font-weight: $weight;
    src: url('../../assets/fonts/#{$font-name}/#{$font-name}.woff') format('woff'),
      url('../../assets/fonts/#{$font-name}/#{$font-name}.ttf') format('ttf'),
      url('../../assets/fonts/#{$font-name}/#{$font-name}.svg') format('svg');
  }
}

@mixin animation($translateX, $scale) {
  transform: translateX($translateX) scale($scale) rotate(45deg);
}

@keyframes slide-right {
  0%,
  100% {
    @include animation(0, 1);
  }

  50% {
    @include animation(4px, 1.2);
  }
}

@keyframes slide-left {
  0% {
    @include animation(0, 1);
  }

  50% {
    @include animation(-4px, 1.2);
  }

  100% {
    @include animation(0, 1);
  }
}

@import '~jquery-ui/themes/base/selectmenu';

.dropdown {
  + .ui-selectmenu-button.ui-button {
    background-color: $light-gray;
    border-radius: 0.25rem;
    height: 1.75rem;
    position: relative;
    transition: box-shadow 0.3s;
    width: 100%;
    outline: none;
    cursor: pointer;

    &:hover {
      box-shadow: 0 0 0.3125rem #000;
    }

    .ui-selectmenu-text {
      color: $gray;
      font-family: 'Source Sans Pro', Arial, sans-serif;
      font-size: 0.9375rem;
      letter-spacing: 0.00875rem;
      padding-left: 0.75rem;
      padding-top: 0.25rem;
    }

    .ui-selectmenu-icon {
      background-color: $cyan;
      border: 0;
      border-radius: 0 0.25rem 0.25rem 0;
      float: right;
      height: 1.75rem;
      outline: none;
      position: relative;
      width: 2.0625rem;

      &:hover {
        box-shadow: 0 0 0.3125rem #000;
      }

      &:active {
        box-shadow: none;
      }

      &::after {
        content: '';
        border-bottom: 0.125rem solid $white;
        border-left: 0.125rem solid $white;
        height: 0.5rem;
        margin-top: 20%;
        transform: rotate(-45deg);
        width: 0.5rem;
        position: absolute;
        left: 35%;
      }
    }
  }
}

.ui-selectmenu-menu.ui-selectmenu-open {
  background-color: $cyan;
  color: #fff;
  border-radius: 0.2rem;
  padding: 0;
  margin: 0;
  cursor: pointer;

  .ui-menu {
    list-style: none inside;
    padding: 0;
    margin: 0;
    font-size: 0.9rem;

    .ui-menu-item-wrapper {
      padding: 0.4rem 0 0.18rem 1rem;

      &:hover {
        background-color: $gray;
      }
    }
  }
}

.ui-state-disabled {
  display: none;
}
