$cyan: #4eb7a8;
$dark-cyan: #46ab9d;

$orange-red: #e75735;
$dark-orange-red: #d54c2c;

$gray: #888;
$light-gray: #e5e5e5;
$dark-gray: #4f4f4f;

$white: #fff;
$white-smoke: #f2f2f2;
$rose-white: #fdf4f2;

@mixin font-face($font-family, $font, $type) {
  $weight: 400;
  $style: normal;

  @if $font == 'lato' {
    @if $type == 'black' {
      $weight: 900;
    }
  }

  $font-name: '#{$font}-#{$type}';

  @font-face {
    font-family: $font-family;
    font-style: normal;
    font-weight: $weight;
    src: url('../../assets/fonts/#{$font-name}/#{$font-name}.woff') format('woff'),
      url('../../assets/fonts/#{$font-name}/#{$font-name}.ttf') format('ttf'),
      url('../../assets/fonts/#{$font-name}/#{$font-name}.svg') format('svg');
  }
}

@mixin animation($translateX, $scale) {
  transform: translateX($translateX) scale($scale) rotate(45deg);
}

@keyframes slide-right {
  0%,
  100% {
    @include animation(0, 1);
  }

  50% {
    @include animation(4px, 1.2);
  }
}

@keyframes slide-left {
  0% {
    @include animation(0, 1);
  }

  50% {
    @include animation(-4px, 1.2);
  }

  100% {
    @include animation(0, 1);
  }
}

.block-sign {
  text-align: center;

  &__name {
    color: $dark-gray;
    display: inline-block;
    font-size: 1.1rem;
    font-weight: 900;
    letter-spacing: 0.1rem;
    text-transform: uppercase;
  
    &_type {
      &_for-charts {
        letter-spacing: 0.065rem;
      }
  
      &_for-search-dropdown {
        letter-spacing: 0.05rem;
        margin-right: 0.3rem;
      }
  
      &_for-profile {
        letter-spacing: 0.07rem;
        position: relative;
        right: 0.5rem;
      }
  
      &_for-sliders {
        left: 0.5625rem;
        position: relative;
      }
  
      &_for-calendar {
        letter-spacing: 0.07rem;
        margin-right: 0.35rem;
      }
  
      &_for-messaging {
        margin-right: 0.8rem;
        letter-spacing: 0.07rem;
      }
  
      &_for-video-box {
        margin-right: 0.125rem;
      }
    }
  }
}
