$cyan: #4eb7a8;
$dark-cyan: #46ab9d;

$orange-red: #e75735;
$dark-orange-red: #d54c2c;

$gray: #888;
$light-gray: #e5e5e5;
$dark-gray: #4f4f4f;

$white: #fff;
$white-smoke: #f2f2f2;
$rose-white: #fdf4f2;

@mixin font-face($font-family, $font, $type) {
  $weight: 400;
  $style: normal;

  @if $font == 'lato' {
    @if $type == 'black' {
      $weight: 900;
    }
  }

  $font-name: '#{$font}-#{$type}';

  @font-face {
    font-family: $font-family;
    font-style: normal;
    font-weight: $weight;
    src: url('../../assets/fonts/#{$font-name}/#{$font-name}.woff') format('woff'),
      url('../../assets/fonts/#{$font-name}/#{$font-name}.ttf') format('ttf'),
      url('../../assets/fonts/#{$font-name}/#{$font-name}.svg') format('svg');
  }
}

@mixin animation($translateX, $scale) {
  transform: translateX($translateX) scale($scale) rotate(45deg);
}

@keyframes slide-right {
  0%,
  100% {
    @include animation(0, 1);
  }

  50% {
    @include animation(4px, 1.2);
  }
}

@keyframes slide-left {
  0% {
    @include animation(0, 1);
  }

  50% {
    @include animation(-4px, 1.2);
  }

  100% {
    @include animation(0, 1);
  }
}

.header {
  border: 0.2rem solid $dark-gray;
  box-sizing: border-box;
  height: 4rem;

  @media screen and (max-width: 600px) {
    height: 6rem;
  }

  &_color {
    &_orange-red {
      background-color: $orange-red;
    }

    &_cyan {
      background-color: $cyan;
    }
  }

  &__name {
    border-bottom: 0;
    border-right: 0.2rem solid $dark-gray;
    box-sizing: border-box;
    color: $white;
    float: left;
    font-size: 2rem;
    height: 100%;
    line-height: 4rem;
    text-align: center;
    width: 12rem;

    @media screen and (max-width: 600px) {
      border: 0;
      float: none;
      height: 60%;
      width: 100%;
    }
  }

  &__navigation {
    align-items: center;
    display: flex;
    float: left;
    height: 100%;
    justify-content: space-around;
    width: calc(100% - 12rem);

    @media screen and (max-width: 600px) {
      border-top: 0.2rem solid $dark-gray;
      float: none;
      height: 40%;
      width: 100%;
    }
  }

  &__link {
    color: $white;
    font-size: 1.5rem;
    letter-spacing: 0.3rem;
    text-decoration: none;

    @media screen and (max-width: 500px) {
      letter-spacing: 0;
    }

    @media screen and (max-width: 400px) {
      font-size: 1.3rem;
    }

    &:hover {
      color: $gray;
    }
  }
}
