$cyan: #4eb7a8;
$dark-cyan: #46ab9d;

$orange-red: #e75735;
$dark-orange-red: #d54c2c;

$gray: #888;
$light-gray: #e5e5e5;
$dark-gray: #4f4f4f;

$white: #fff;
$white-smoke: #f2f2f2;
$rose-white: #fdf4f2;

@mixin font-face($font-family, $font, $type) {
  $weight: 400;
  $style: normal;

  @if $font == 'lato' {
    @if $type == 'black' {
      $weight: 900;
    }
  }

  $font-name: '#{$font}-#{$type}';

  @font-face {
    font-family: $font-family;
    font-style: normal;
    font-weight: $weight;
    src: url('../../assets/fonts/#{$font-name}/#{$font-name}.woff') format('woff'),
      url('../../assets/fonts/#{$font-name}/#{$font-name}.ttf') format('ttf'),
      url('../../assets/fonts/#{$font-name}/#{$font-name}.svg') format('svg');
  }
}

@mixin animation($translateX, $scale) {
  transform: translateX($translateX) scale($scale) rotate(45deg);
}

@keyframes slide-right {
  0%,
  100% {
    @include animation(0, 1);
  }

  50% {
    @include animation(4px, 1.2);
  }
}

@keyframes slide-left {
  0% {
    @include animation(0, 1);
  }

  50% {
    @include animation(-4px, 1.2);
  }

  100% {
    @include animation(0, 1);
  }
}

.percentages {
  font-size: 1.1rem;

  &__title {
    color: $dark-gray;
    font-size: 1.3rem;
    margin-bottom: 0.5rem;
    text-align: center;
  }

  &__chart {
    align-items: center;
    display: flex;
    justify-content: flex-start;
  }

  &__sign {
    margin-left: 1.875rem;

    @media screen and (max-width: 400px) {
      margin-left: 0;
    }
  }
}
