$cyan: #4eb7a8;
$dark-cyan: #46ab9d;

$orange-red: #e75735;
$dark-orange-red: #d54c2c;

$gray: #888;
$light-gray: #e5e5e5;
$dark-gray: #4f4f4f;

$white: #fff;
$white-smoke: #f2f2f2;
$rose-white: #fdf4f2;

@mixin font-face($font-family, $font, $type) {
  $weight: 400;
  $style: normal;

  @if $font == 'lato' {
    @if $type == 'black' {
      $weight: 900;
    }
  }

  $font-name: '#{$font}-#{$type}';

  @font-face {
    font-family: $font-family;
    font-style: normal;
    font-weight: $weight;
    src: url('../../assets/fonts/#{$font-name}/#{$font-name}.woff') format('woff'),
      url('../../assets/fonts/#{$font-name}/#{$font-name}.ttf') format('ttf'),
      url('../../assets/fonts/#{$font-name}/#{$font-name}.svg') format('svg');
  }
}

@mixin animation($translateX, $scale) {
  transform: translateX($translateX) scale($scale) rotate(45deg);
}

@keyframes slide-right {
  0%,
  100% {
    @include animation(0, 1);
  }

  50% {
    @include animation(4px, 1.2);
  }
}

@keyframes slide-left {
  0% {
    @include animation(0, 1);
  }

  50% {
    @include animation(-4px, 1.2);
  }

  100% {
    @include animation(0, 1);
  }
}

.user-profile {
  max-width: 23rem;
  min-width: 15rem;
  display: inline-flex;
  justify-content: flex-start;
  align-items: center;

  &__tooltip {
    border-radius: 0.5rem;
    text-align: center;
    margin-left: 0.7rem;
    padding: 0 0.35rem;
    position: relative;

    &::after {
      border-radius: 0.125rem;
      border-width: 0.375rem;
      content: '';
      height: 0.625rem;
      margin-top: -0.375rem;
      position: absolute;
      right: calc(100% - 0.34375rem);
      top: calc(50% + 0.1875rem);
      transform: rotate(45deg);
      width: 0.625rem;
    }

    &_color {
      &_cyan {
        background-color: $cyan;

        &::after {
          background-color: $cyan;
        }
      }

      &_grey {
        background-color: $light-gray;

        &::after {
          background-color: $light-gray;
        }
      }
    }
  }

  &__name {
    display: block;
    font-size: 1.125rem;
    line-height: 1.125rem;
    margin-bottom: 0.25rem;
    margin-top: 0.75rem;

    &_color {
      &_orange-red {
        color: $orange-red;
      }

      &_white {
        color: $white;
      }
    }
  }

  &__job {
    color: $dark-gray;
    display: block;
    font-size: 0.6875rem;
    letter-spacing: 0.034375rem;
    line-height: 0.6875rem;
    text-transform: uppercase;

    &_color {
      &_black {
        color: $dark-gray;
      }

      &_white {
        color: $white;
      }
    }
  }

  &__links-container {
    display: flex;
    justify-content: space-around;
    margin: auto;
    width: 76%;

    @media screen and (max-width: 400px) {
      width: 85%;
    }
  }

  &__link {
    background-position: center;
    background-repeat: no-repeat;
    border-radius: 50%;
    display: inline-block;
    height: 2.1875rem;
    position: relative;
    width: 2.1875rem;
    margin: 0.5rem;

    &::after {
      content: '';
      height: 1.875rem;
      left: 0;
      opacity: 0.5;
      position: absolute;
      top: 0;
      width: 1.875rem;
    }

    &_theme {
      &_cyan {
        background-color: rgba(20, 202, 130, 0);
        transition: background-color 1s;

        &:nth-of-type(1) {
          background-image: url('images/fb-gray.png');
        }

        &:nth-of-type(2) {
          background-image: url('images/twitter-gray.png');
        }

        &:nth-of-type(3) {
          background-image: url('images/dribbble-gray.png');
        }

        &:hover {
          background-color: rgba(20, 202, 130, 1);
        }

        &:active {
          background-color: rgba(20, 202, 130, 0);
        }
      }

      &_orange-red {
        background-color: rgba(216, 187, 71, 0);
        transition: background-color 1s;

        &:nth-of-type(1) {
          background-image: url('images/fb-white.png');
        }

        &:nth-of-type(2) {
          background-image: url('images/twitter-white.png');
        }

        &:nth-of-type(3) {
          background-image: url('images/dribbble-white.png');
        }

        &:hover {
          background-color: rgba(196, 186, 186, 0.5);
        }

        &:active {
          background-color: rgba(196, 186, 186, 0);
        }
      }
    }
  }
}
